
import { defineComponent } from "vue";

export default defineComponent({
  props: {
    card: {
      type: Object,
      required: true,
    },
  },
  setup(props) {
    const d = new Date(props.card.publishedAt);
    const months = [
      "января",
      "февраля",
      "марта",
      "апреля",
      "мая",
      "июня",
      "июля",
      "августа",
      "сентября",
      "октября",
      "ноября",
      "декабря",
    ];
    const day = d.getDate();
    const monthName = months[d.getMonth()];
    const year = d.getFullYear();
    const date = `${day} ${monthName} ${year}`;
    return { date };
  },
});
