
import { defineComponent } from "vue";
import Flickity from "vue-flickity/src/flickity.vue";

export default defineComponent({
  components: {
    Flickity,
  },
  data() {
    return {
      flickityOptions: {
        initialIndex: 0,
        contain: true,
        prevNextButtons: false,
        pageDots: true,
        draggable: true,
        freeScroll: false,
      },
    };
  },
  setup() {},
});
